'use client'

import { useState, useRef, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { auth, db } from '@/lib/firebaseConfig'
import { useAuthState } from 'react-firebase-hooks/auth'
import { doc, setDoc, getDoc } from 'firebase/firestore'

interface Message {
  id: string
  text: string
  sender: 'user' | 'assistant'
  timestamp: Date
  type?: 'text' | 'tutorial'
  tutorialId?: string
}

import tutorials from '@/tutoriallibrary/tutorialData'


export default function FloatingChatbot() {
  const [isOpen, setIsOpen] = useState(false)
  const [messages, setMessages] = useState<Message[]>([])
  const [inputText, setInputText] = useState('')
  const [isListening, setIsListening] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const router = useRouter()
  const [user] = useAuthState(auth)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  // Load previous chat history for logged in users
  useEffect(() => {
    if (!user) return
    const fetchHistory = async () => {
      const docRef = doc(db, 'users', user.uid)
      const docSnap = await getDoc(docRef)
      if (docSnap.exists()) {
        setMessages(docSnap.data().chatHistory?.map((m: any) => ({ ...m, timestamp: new Date(m.timestamp) })) || [])
      }
    }
    fetchHistory()
  }, [user])

  // AI logic with tutorial suggestions
  const getLocalResponse = (text: string) => {
    const lower = text.toLowerCase()

    if (lower.includes('facebook')) {
      const tutorial = tutorials.find(t => t.title.toLowerCase().includes('facebook'))
      return tutorial
        ? { text: "I found a Facebook tutorial for you.", type: 'tutorial', tutorialId: tutorial.id }
        : { text: "To post on Facebook, click the 'What's on your mind?' box.", type: 'text' }
    }

    if (lower.includes('whatsapp')) {
      const tutorial = tutorials.find(t => t.title.toLowerCase().includes('whatsapp'))
      return tutorial
        ? { text: "Here's a WhatsApp tutorial for you.", type: 'tutorial', tutorialId: tutorial.id }
        : { text: "Open a chat and tap the + icon to send a photo on WhatsApp.", type: 'text' }
    }

    if (lower.includes('instagram')) {
      const tutorial = tutorials.find(t => t.title.toLowerCase().includes('instagram'))
      return tutorial
        ? { text: "I found an Instagram tutorial for you.", type: 'tutorial', tutorialId: tutorial.id }
        : { text: "Instagram lets you share photos and videos easily.", type: 'text' }
    }

    return { text: "I'm not sure about that. Try asking something else!", type: 'text' }
  }

  const handleSend = () => {
    if (!inputText.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputText,
      sender: 'user',
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setInputText('')

    setTimeout(async () => {
      const response = getLocalResponse(userMessage.text)
      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: response.text,
        sender: 'assistant',
        timestamp: new Date(),
        type: response.type,
        tutorialId: response.tutorialId
      }
      setMessages(prev => [...prev, assistantMessage])

      // Save chat history for logged in users
      if (user) {
        const docRef = doc(db, 'users', user.uid)
        await setDoc(docRef, { chatHistory: [...messages, userMessage, assistantMessage] }, { merge: true })
      }
    }, 500)
  }

  const handleVoiceInput = () => {
    if (!('webkitSpeechRecognition' in window)) {
      alert("Voice recognition is not supported in your browser. Please use Chrome or Edge.")
      return
    }
    const recognition = new (window as any).webkitSpeechRecognition()
    recognition.continuous = false
    recognition.interimResults = false

    recognition.onstart = () => setIsListening(true)
    recognition.onresult = (event: any) => {
      setInputText(event.results[0][0].transcript)
      setIsListening(false)
    }
    recognition.onerror = () => setIsListening(false)
    recognition.onend = () => setIsListening(false)
    recognition.start()
  }

  const quickQuestions = [
    "How to post on Facebook?",
    "Send photos on WhatsApp",
    "Video call my family",
    "What is Instagram?",
    "Make my account safe"
  ]

  return (
    <>
      {/* Floating Chat Button */}
      {!isOpen && (
        <button
          onClick={() => setIsOpen(true)}
          className="fixed bottom-6 right-6 w-16 h-16 bg-blue-600 hover:bg-blue-700 rounded-full shadow-2xl flex items-center justify-center text-white transition-all duration-300 hover:scale-110 z-50"
        >
          ðŸ¤–
        </button>
      )}

      {/* Chat Modal */}
      {isOpen && (
        <div className="fixed bottom-6 right-6 w-96 h-[600px] bg-white rounded-2xl shadow-2xl flex flex-col z-50 border border-gray-200">
          {/* Header */}
          <div className="bg-blue-600 text-white p-4 rounded-t-2xl flex justify-between items-center">
            <h3 className="font-semibold text-lg">ElderEase Assistant</h3>
            <button onClick={() => setIsOpen(false)} className="text-white text-2xl">Ã—</button>
          </div>

          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4">
            {messages.map(message => (
              <div key={message.id} className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}>
                <div className={`max-w-[80%] rounded-2xl p-3 ${message.sender === 'user' ? 'bg-blue-600 text-white rounded-br-none' : 'bg-gray-100 text-gray-800 rounded-bl-none'}`}>
                  <div className="whitespace-pre-wrap text-sm">{message.text}</div>

                  {/* Show me how button for tutorials */}
                  {message.type === 'tutorial' && message.tutorialId && (
                    <button
                      onClick={() => router.push(`/tutoriallibrary#${message.tutorialId}`)}
                      className="mt-2 bg-green-600 text-white px-3 py-1 rounded-lg text-sm hover:bg-green-700 transition"
                    >
                      Show me how
                    </button>
                  )}

                  <div className={`text-xs mt-1 ${message.sender === 'user' ? 'text-blue-200' : 'text-gray-500'}`}>
                    {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                  </div>
                </div>
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>

          {/* Quick Questions */}
          <div className="border-t border-gray-200 p-3">
            <p className="text-xs text-gray-600 mb-2">Quick questions:</p>
            <div className="flex flex-wrap gap-1">
              {quickQuestions.map((q, idx) => (
                <button key={idx} onClick={() => { setInputText(q); setTimeout(handleSend, 100) }}
                  className="bg-blue-50 text-blue-700 px-2 py-1 rounded-full text-xs hover:bg-blue-100 transition">{q}</button>
              ))}
            </div>
          </div>

          {/* Input Area */}
          <div className="border-t border-gray-200 p-4 flex space-x-2">
            <input
              type="text"
              value={inputText}
              onChange={e => setInputText(e.target.value)}
              onKeyPress={e => e.key === 'Enter' && handleSend()}
              placeholder="Ask about social media..."
              className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-300 text-sm"
            />
            <button onClick={handleVoiceInput} disabled={isListening} className={`${isListening ? 'text-red-500' : 'text-gray-400 hover:text-blue-600'}`}>
              {isListening ? 'ðŸ”´' : 'ðŸŽ¤'}
            </button>
            <button onClick={handleSend} className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition text-sm font-semibold">
              Send
            </button>
          </div>
        </div>
      )}
    </>
  )
}
